import react, {Component} from 'react';
import Layout from '../../components/layout'
import {Message, MessageBody, MessageHeader, Delete} from 'bloomer'
import { Container, Title} from 'bloomer'
import web3 from '../../ethereum/web3'


export default class Dapps extends Component {
    state ={
        metamask: '',
        message: false,
        errorMessage: ''

    }
    // static async getInitialProps() {
    //     console.log("checking web3")
    
    //   return {metamask: web3}

    // }
    checkMetamask(w){
        console.log(w)
        if(w == ''){
            return <Message>
            <MessageHeader>
                <p>Metamask</p>
                <Delete />
            </MessageHeader>
            <MessageBody>
                Lorem ipsum dolor sit amet, consectetur adipiscing elit. <strong>Pellentesque risus mi</strong>,
                tempus quis placerat ut, porta nec nulla. Vestibulum rhoncus ac ex sit amet fringilla. Nullam gravida
                purus diam, et dictum <a>felis venenatis</a> efficitur. Aenean ac <em>eleifend lacus</em>, in mollis
                lectus. Donec sodales, arcu et sollicitudin porttitor, tortor urna tempor ligula, id porttitor mi magna
                a neque. Donec dui urna, vehicula et sem eget, facilisis sodales sem.
            </MessageBody>
        </Message>

        }
       
           
    


   
    }
    render() {
        console.log("what is in ", web3)
        return ( <Layout>

            <Container>
            <Title isSize = {4}> The playground page </Title>
             <p> Ethereum playground </p>
             {this.checkMetamask(web3)}
            
             
              </Container> </Layout>)
        }

    }


